"use client";
import React from "react";
import { PieChart, Pie, Tooltip, Cell, ResponsiveContainer } from "recharts";

// Data yang sudah ada
const travelData = [
  { nama: "Dr. Andi", role: "Dokter umum", distance: "12 km", visits: 8 },
  { nama: "Dr. Siti", role: "Perawat", distance: "15 km", visits: 8 },
  { nama: "Dr. Budi", role: "Dokter Spesialis", distance: "8.7 km", visits: 5 },
];

// Definisikan warna untuk setiap sektor pie chart
const colors = ["#38bdf8", "#60a5fa", "#3b82f6"];

const HighDemandAcsess = () => {
  return (
    <div className="bg-[#1e1e1e] p-6 rounded-2xl shadow-lg border border-[#1f1f1f]">
      <h2 className="text-white text-lg font-semibold mb-4">Nakes Travel Efficiency</h2>
      <div className="w-full h-64 flex justify-center">
        <ResponsiveContainer width="95%" height="100%">
          <PieChart>
            <Pie
              data={travelData}
              dataKey="visits"
              nameKey="nama"
              cx="50%"
              cy="50%"
              outerRadius="80%"
              label
            >
              {travelData.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
              ))}
            </Pie>
            <Tooltip
              contentStyle={{ backgroundColor: "#334155", border: "none" }}
              itemStyle={{ color: "#38bdf8" }}
            />
          </PieChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
};

export default HighDemandAcsess;
